import { State } from 'vuex-class';
import banner from '../../components/Banner.vue';

@Component({
  components: {
    banner,
  },
})

export default class ExpiredPoints extends Vue {
  @State('points', { namespace: 'task' }) statePoints!: IPointsItem[];

  link() {
    this.$router.push('/activity/points_record');
